import React from 'react';
import PropTypes from 'prop-types';
import { toString } from 'lodash';
import { t } from 'sn-translate';

import '@servicenow/now-input';
import '@devsnc/sn-record-control';
import '@servicenow/now-popover';
import react from '@servicenow/ui-renderer-react';
import rtlBehavior from '@servicenow/behavior-rtl';
import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import {
	time,
	isAttrTrue,
	fieldPropTypes,
	withValueChangeCallbacks,
	getDeclarativeActionsProps,
	transformState,
	ForwardProperties,
	transformMessages
} from '@devsnc/sn-controls-common';

import {
	CalendarView,
	getFormat,
	isDateIncluded,
	constants as miniCalConsts
} from '@servicenow/now-record-mini-calendar';

import style from './date.scss';

const POPOVER_OPENED_SET = 'NOW_POPOVER#OPENED_SET';
const DATE_PICKER_OPENED_SET = 'NOW_RECORD_DATE_PICKER#OPENED_SET';
const PRESET_MESSAGE_UPDATED = 'SN_DATE_PRESET_PANEL#MESSAGE_UPDATED';
const { COMPONENT_PROPERTY_CHANGED } = actionTypes;
const { STANDARD_TYPE, WEEKLY_TYPE } = miniCalConsts;

/**
 * Allows users to input date (and time) values either by direct import or via
 * a calendar popup.
 */
export class NowDatePicker extends React.PureComponent {
	static componentName = 'now-record-date-picker';
	constructor(props) {
		super(props);
		this.state = {
			focusToCalendarButton: false,
			opened: false,
			pendingValue: this.props.value,
			selectedTimeAgo: {},
			value: this.props.value
		};
		this.controlRef;
	}

	static style = style;

	static defaultProps = {
		firstDayOfWeek: 1,
		bottomBar: true,
		timePicker: false,
		language: 'en',
		timeAgo: false,
		type: STANDARD_TYPE
	};

	showBottomBar = () => {
		const { bottomBar, timePicker, hasTimePicker } = this.props;
		return !!(bottomBar || timePicker || hasTimePicker);
	};

	isRTL = dir => 'rtl' === dir;

	/**
	 * Convert the value (string) into a moment date object
	 */
	getDateObject(
		value,
		format = getFormat(this.props),
		language = this.props.language
	) {
		const date = time(language)(value, format, true);
		return date.isValid() ? date : null;
	}

	saveValue = (inputStr, selectedTimeAgo) => {
		const date = this.getDateObject(inputStr);
		if (this.props.type === WEEKLY_TYPE && date) {
			const week = date.week();
			const dayOfMonth = date.date();
			///pad 1 year for last week of year
			if (week === 1 && dayOfMonth > 1) {
				date.add('1', 'year');
			}
		}
		const format = getFormat(this.props);
		const value = date ? date.format(format) : inputStr;

		let error = null;
		const disabledDates = this.props.disabledDates;

		if (inputStr && !this.props.timeAgo && !date) {
			error = {
				type: 'error',
				message: t(
					'Invalid date {0}. Must be a valid date in the format {1}',
					inputStr,
					format
				)
			};
		} else if (inputStr && disabledDates) {
			const isADisabledDate = isDateIncluded(
				inputStr,
				getFormat(this.props),
				this.props.disabledDates
			);
			if (isADisabledDate) {
				error = {
					type: 'error',
					message: t(
						'Invalid selection. Choose another date.',
						inputStr,
						format
					)
				};
			}
		}

		this.props.onValueChange({
			value,
			timeAgo: selectedTimeAgo,
			error
		});
		return value;
	};

	/**
	 * Closes the popup and passes up the state up to the parent
	 */
	onOk = () => {
		const value = this.saveValue(
			this.state.pendingValue,
			this.state.selectedTimeAgo
		);

		this.props.dispatch(DATE_PICKER_OPENED_SET, {
			value: false,
			savePending: true
		});
	};

	onCancel = () => {
		this.resetDate();
		this.props.dispatch(DATE_PICKER_OPENED_SET, { value: false });
	};

	componentWillReceiveProps(newProps) {
		const newOpened = newProps.opened || false;

		if (newOpened === false && this.state.opened === true) {
			this.setState({ focusToCalendarButton: true });
		}

		if (
			newProps.value !== this.state.value ||
			newProps.language !== this.props.language ||
			newOpened !== this.state.opened
		) {
			this.setState({
				value: newProps.value,
				pendingValue: !newProps.savePending
					? newProps.value
					: this.state.pendingValue,
				opened: newOpened,
				selectedTimeAgo: {}
			});
		}
	}

	/**
	 * Reset the date back to the original
	 */
	resetDate = () => {
		this.setState({
			pendingValue: this.state.value
		});

		this.props.onStagedValueChange({ value: this.state.value });
	};

	restrictEntry = e => {
		//no manual entry for timeAgo
		if (
			this.props.type === STANDARD_TYPE &&
			this.props.timeAgo &&
			e.key !== 'Escape' &&
			e.key !== 'Enter' &&
			e.key !== 'Tab'
		) {
			e.preventDefault();
			e.stopPropagation();
		}
	};
	/**
	 * Called as the user interacts with the input field. These changes
	 * are persisted as the user types, unlike dropdown interaction
	 */
	onInputChange = event => {
		const path = event.path || (event.composedPath && event.composedPath());
		const value = path[0].value;
		this.setState({
			pendingValue: value
		});

		this.props.onStagedValueChange({ value });
	};

	onInputBlur = () => {
		const value = this.saveValue(this.state.pendingValue);
		this.setState({
			value,
			pendingValue: value
		});
	};

	/**
	 * Called as the user interacts with the dropdown. These changes
	 * are not persisted unless the user explicitly clicks OK.
	 */
	onDateChange = (pendingValue, selectedTimeAgo, setPopover = true) => {
		const { timeAgo } = this.props;
		this.props.onStagedValueChange({ value: pendingValue });
		let value = this.state.value;

		const hasBottomBar = this.showBottomBar();
		if (!hasBottomBar) {
			// the calendar should still be open if the user is interacting with an empty
			// date picker, even if it is bottomless. if there is a value, it will close
			// when date changes again as expected
			// value is defaulted to the current date/time for absolute dates
			// for time ago close popover in any case
			if (setPopover) {
				const close = timeAgo ? false : !value;
				this.props.dispatch(DATE_PICKER_OPENED_SET, {
					value: this.props.opened && close
				});
			}
			value = this.saveValue(pendingValue, selectedTimeAgo);
		}

		this.setState({
			pendingValue,
			value,
			selectedTimeAgo
		});
	};

	onFocus = () => {
		if (this.controlRef && this.state.focusToCalendarButton) {
			this.controlRef.shadowRoot.querySelector('now-button').focus();
			this.setState({ focusToCalendarButton: false });
		}
	};

	render() {
		let {
			componentId,
			hasTimePicker,
			name,
			label,
			messages,
			description,
			invalid,
			required,
			reversed = this.isRTL(this.props.dir),
			readonly,
			timePicker,
			autofocus,
			dispatch,
			timeAgo
		} = this.props;

		let { opened, pendingValue } = this.state;

		name = `${name}-date`;
		const isReadonly = isAttrTrue(readonly);
		const descriptionId = `${componentId}-keyboard-instructions`;
		const declarativeActionsProps = getDeclarativeActionsProps(
			this.props.declarativeActionsProps,
			this.props
		);

		return (
			<now-popover
				hide-tail
				interaction-type="dialog"
				positions={[
					{ target: 'bottom-start', content: 'top-start' },
					{ target: 'top-start', content: 'bottom-start' },
					{ target: 'bottom-end', content: 'top-end' },
					{ target: 'top-end', content: 'bottom-end' }
				]}
				opened={opened}
				manageOpened
				offset={2}
				append-to-payload={{ id: 'date-picker-popover' }}
			>
				<now-input
					manage-value
					ref={ref => ref.addEventListener('focus', this.onFocus)}
					label={label}
					value={toString(pendingValue)}
					name={name}
					slot="trigger"
					placeholder={getFormat(this.props)}
					messages={transformMessages(messages)}
					description={description}
					required={required}
					reversed={reversed}
					autofocus={autofocus}
					// to stop popover from opening when input is clicked
					// since the full input is the popover trigger. With this,
					// the popover will only open when the sn-record-control
					// is triggered.
					onclick={e => e.stopPropagation()}
					oninput={this.onInputChange}
					onblur={this.onInputBlur}
					onkeydown={this.restrictEntry}
					onmousedown={this.restrictEntry}
					onmouseup={this.restrictEntry}
					invalid={invalid}
					manageInvalid={true}
					aria-haspopup="dialog"
					aria-expanded={opened}
					aria-describedby={descriptionId}
					aria-live="polite"
					aria-relevant="text"
					aria-atomic="true"
					readonly={isReadonly}
				>
					<span slot="end">
						{!isReadonly && (
							<sn-record-control
								icon="calendar-outline"
								tooltip={t('Show calendar')}
								ref={ref => (this.controlRef = ref)}
								onClick={() =>
									dispatch(DATE_PICKER_OPENED_SET, { value: !opened })
								}
								size="sm"
							/>
						)}
					</span>
					<sn-declarative-field-action
						slot="end"
						model={declarativeActionsProps}
						actions={declarativeActionsProps.declarativeUiActions}
						dispatch={this.props.dispatch}
						formData={this.props.formData}
						fetchDeclarativeActions={
							declarativeActionsProps.fetchDeclarativeActions
						}
					/>
				</now-input>
				<CalendarView
					slot="content"
					type={this.props.type}
					name={`${name}-calendar-popup`}
					bottomBar={this.showBottomBar()}
					onDateChange={this.onDateChange}
					onOk={this.onOk}
					onCancel={this.onCancel}
					value={this.state.pendingValue}
					format={getFormat(this.props)}
					language={this.props.language}
					timePicker={timePicker || hasTimePicker}
					utcOffset={this.props.utcOffset}
					firstDayOfWeek={this.props.firstDayOfWeek - 1}
					reversed={reversed}
					defaultZeros={true}
					opened={opened}
					timeAgo={this.props.type === WEEKLY_TYPE ? false : timeAgo}
					disabledDates={this.props.disabledDates}
					embedded={false}
				/>
				<span
					id={descriptionId}
					className="now-a11y-label"
					aria-live="polite"
					aria-relevant="text"
				>
					{t(
						opened
							? 'Press Tab to focus calendar dialog. Press Escape to close calendar dialog.'
							: 'Press Enter to open calendar dialog.'
					)}
				</span>
			</now-popover>
		);
	}
}

NowDatePicker.propTypes = {
	...fieldPropTypes,

	disabledDates: PropTypes.array,

	firstDayOfWeek: PropTypes.oneOf([1, 2, 3, 4, 5, 6, 7]),

	format: PropTypes.string,

	hasTimePicker: PropTypes.bool,

	timePicker: PropTypes.bool,

	timeAgo: PropTypes.oneOfType([PropTypes.bool, PropTypes.object]),

	type: PropTypes.string,

	language: PropTypes.string
};

const DefaultNowDatePicker = withValueChangeCallbacks(
	{ stopBubbling: ['SN_RECORD_INPUT'] },
	NowDatePicker
);

export default DefaultNowDatePicker;

/**
 *
 * This component allows users to specify a date, or pick a date from a calendar
 *
 * ```jsx
 * <now-record-date-picker label="Pick a date" value="2020-02-02" />
 * ```
 *
 * With time picker
 *
 * ```jsx
 * <now-record-date-picker
 *		label="Pick a date and time"
 *		value="2020-02-02 2:45 pm"
 *		format="YYYY-MM-DD hh:mm a"
 *		timePicker
 * />
 * ```
 *
 * @seismicElement now-record-date-picker
 * @summary Date Picker Component
 *
 */
createCustomElement('now-record-date-picker', {
	type: react,
	view: ForwardProperties(DefaultNowDatePicker),
	styles: style,
	properties: {
		/**
		 * Accessible label for the component. Only needed when the `label` prop is not
		 * descriptive enough on its own.
		 *
		 * @type {string}
		 * @private
		 */
		ariaLabel: {},

		/**
		 * Which element (by ID) acts as a label for the component
		 *
		 * @type {string}
		 * @private
		 */
		ariaLabelledby: {},

		/**
		 * Which element (by ID) provides more description for the component
		 *
		 * @type {string}
		 * @private
		 */
		ariaDescribedby: {},

		/**
		 * An object whose items are all aria properties in the format 'aria-*' : 'value'
		 * @type {{'aria-*': string }}
		 */
		configAria: {},

		/**
		 * Causes the component to receive focus as soon as it has finished
		 * mounting.
		 * When passed as a string, "true" (case-sensitive) will be treated like
		 * boolean true.
		 *
		 * @type {(boolean|string)}
		 */
		autofocus: {},

		/**
		 * Whether or not to display the bottom bar containing the Ok and Cancel buttons.
		 *
		 * @type {boolean}
		 */
		bottomBar: { default: true },

		/**
		 * Additional content describing the field, usually to provide further
		 * instruction to the user.
		 *
		 * @type {string}
		 */
		description: {},

		/**
		 * Array of disabled dates defined as
		 * `[ (DATE | DATE_RANGE)* ]`
		 *
		 * `DATE`:
		 * 	Date string matching `format` property
		 *
		 * `DATE_RANGE`:
		 * 	{ start: `DATE`, end: `DATE` } | { after: `DATE` } | { before: `DATE` }
		 *
		 * where `start`/`end` are inclusive and `after`/`before` are exclusive
		 * eg. [ '13-01-2020', { start: '15-01-2020', end: '20-01-2020' }, { before: '05-01-2020' }, { after: '25-01-2020' }]
		 *
		 * @type { Array < string | {start: string, end: string} | {before: string} | {after: string} > }
		 * @see [Moment format string](https://momentjs.com/docs/#/displaying/format/).
		 */
		disabledDates: {
			default: [],
			schema: {
				type: 'array',
				items: [
					{ type: 'string' },
					{
						type: 'object',
						properties: { start: { type: 'string' }, end: { type: 'string' } },
						required: ['start', 'end']
					},
					{ type: 'object', properties: { before: { type: 'string' } } },
					{ type: 'object', properties: { after: { type: 'string' } } }
				]
			}
		},

		/**
		 * Name of the field
		 *
		 * @type {any}
		 * @private
		 */
		fieldName: {},

		/**
		 *
		 * Type of the field
		 *
		 * @type {string}
		 * @private
		 */
		fieldType: {},

		/**
		 * What day of the week to begin the calendar. 1 = Sunday, 2 = Monday, etc.
		 * Can only be a value from 1 to 7
		 *
		 * @type {number}
		 */
		firstDayOfWeek: {},

		/**
		 * Format describing how to serialize/deserialize the `value` prop.
		 * This property is only relevant for `type='standard'` and is ignored for other views.
		 *
		 * When not provided, the default format is one of:
		 *
		 * - When showing a time picker: `YYYY-MM-DD hh:mm:ss a`
		 * - When not showing a time picker: `YYYY-MM-DD`
		 * - For weekly type, `[W]ww YYYY`
		 * - For yearly type, `YYYY`
		 *
		 * @see [Moment format string](https://momentjs.com/docs/#/displaying/format/).
		 * @type {string}
		 */
		format: {},

		/**
		 * Toggles whether or not to show the TimeAgo Preset Panel
		 *
		 * Passing in false will hide the panel altogether.
		 * Passing in true will show the panel with default config which is {absolute: false, between: false, tableName: ''}.
		 * Passing in a configObject will show the panel with the config as passed in.
		 *
		 * absolute - resolve relative dates to absolute dates and return in addition to the relative dates on dispatch.
		 * between - flag to send back the 'between' instead of 'end' of relative time ago
		 * tableName - tableName for the Business Calendar Relative fields.
		 *
		 * @type {(boolean|{absolute: boolean, between: boolean, tableName: string, value: string})}
		 *
		 */
		timeAgo: {
			default: false,
			schema: {
				oneOf: [
					{ type: 'boolean' },
					{
						type: 'object',
						properties: {
							absolute: { type: 'boolean' },
							between: { type: 'boolean' },
							tableName: { type: 'string' }
						}
					}
				]
			}
		},

		/**
		 * Please use `timePicker` instead
		 *
		 * @type {boolean}
		 * @private
		 */
		hasTimePicker: {},

		/**
		 * formData object containing glide form data
		 *
		 * @type {object}
		 */
		formData: {},

		/**
		 * Whether or not to display the time picker along with the calendar.
		 *
		 * @type {boolean}
		 */
		timePicker: {},

		/**
		 * The UTC offset
		 *
		 * @type {number}
		 */
		utcOffset: {},

		/**
		 * Specifies whether the current value is not valid.
		 * When passed as a string, "true" (case-sensitive) will be treated like
		 * boolean true.
		 *
		 * @type {(boolean|string)}
		 */
		invalid: {},

		/**
		 * It's a label! All form controls need a label!
		 *
		 * @type {string}
		 */
		label: {},

		/**
		 * User language preference. Used for moment date formatting
		 *
		 * @type {string}
		 */
		language: {},

		/**
		 * Array of message objects that must contain type and message itself.
		 *
		 * @type {Array<{type: ("error"|"warning"|"success"|"liveUpdate"|"info"|"suggestion"), type:Array<string,Object<{string, getMessage:function}>> }>}
		 */
		messages: {},

		/**
		 * Name for the component. Any events dispatched by the component will use this name.
		 *
		 * @type {string}
		 */
		name: {},

		/**
		 * Callback used when an intermediate value has changed.
		 *
		 * @type {function}
		 * @private
		 */
		onStagedValueChange: {},

		/**
		 * Callback used when the value has changed. Very similar to an `onChange`
		 * event, but this may also fire when the component formats its value
		 * onBlur.
		 *
		 * @param {object} event - The event payload
		 * @param {string} event.name - The name of the component that triggered the event
		 * @param {any}    event.value - The new value for the component
		 * @param {string} event.displayValue - The human-readable represenation of the value
		 *
		 * @param {object} event.error - If the current value is invalid, this propery will be set. When the current value is valid, this property will be null.
		 * @param {string} event.error.type - Error type
		 * @param {string} event.error.message - Description of why the value is invalid
		 *
		 * @type {function}
		 * @private
		 */
		onValueChange: {},

		/**
		 * Content to display when there is no suitable display value.
		 *
		 * @type {string}
		 */
		placeholder: {},

		/**
		 * Specifies that the user cannot modify the value of the component.
		 * When passed as a string, "true" (case-sensitive) will be treated like
		 * boolean true.
		 *
		 * @type {(boolean|string)}
		 */
		readonly: {},

		/**
		 * sys_id of the current record where the field is located
		 *
		 * @type {string}
		 */
		recordSysId: {},

		/**
		 * Whether or not the user must fill in a value for this component.
		 * Can be specified as a boolean or the string "true"
		 *
		 * @type {boolean|string}
		 */
		required: {},

		/**
		 * Specifies that the field should be rendered right-to-left.
		 * When passed as a string, "true" (case-sensitive) will be treated like
		 * boolean true.
		 *
		 * @type {boolean|string}
		 * @private
		 */
		reversed: {},

		/**
		 * Unique name for a shadowDom slot
		 *
		 * @type {string}
		 * @private
		 */
		slot: {},

		/**
		 * Name of the table the reference field is attached to
		 *
		 * @type {string}
		 */
		tableName: {},

		/**
		 * Type determines the display view of the date-range-picker.
		 * Supported values are `standard`, `weekly`, `monthly`,`quarterly` and `yearly`.
		 * @type {String}
		 */
		type: {
			default: 'standard',
			schema: {
				type: 'string',
				enum: ['standard', 'weekly', 'monthly', 'quarterly', 'yearly']
			}
		},

		/**
		 * The initial date/date-time value. This value must be the localized time, rather than UTC time.
		 *
		 * @type {string}
		 */
		value: {},

		/**
		 * Declarative Action props
		 * @type {object}
		 */
		declarativeActionsProps: {}
	},
	behaviors: [{ behavior: rtlBehavior }],
	actionHandlers: {
		[POPOVER_OPENED_SET]: {
			effect: ({ action, updateState }) => {
				//DEF0119521: Handle Closing of the Popover only when
				//events (escape) are on the date picker popover and not on its underlying components
				//like now-dropdown or such
				const { id, value } = action.payload;

				//check only date picker popover
				if (id === 'date-picker-popover') {
					updateState({
						opened: value
					});
				}
			},
			stopPropagation: true
		},
		[DATE_PICKER_OPENED_SET]: {
			effect: ({ action, updateState }) => {
				updateState({
					opened: action.payload.value,
					savePending: action.payload.savePending
				});
			},
			stopPropagation: true
		},
		[COMPONENT_PROPERTY_CHANGED]: {
			effect: ({ action, updateState }) => {
				if (action.payload.name === 'value') {
					updateState({
						savePending: false
					});
				}
			}
		},
		[PRESET_MESSAGE_UPDATED]: {
			effect: ({ properties: { messages = [] }, action, updateProperties }) => {
				const message = action.payload;
				if (message) {
					updateProperties({
						messages: [...messages, message]
					});
				}
			}
		}
	},
	dispatches: {
		/**
		 * Dispatched when a different date is chosen prior to clicking the OK button.
		 * @type {{ value: string }}
		 */
		'NOW_RECORD_DATE_PICKER#STAGED_VALUE_CHANGED': {},
		/**
		 * Dispatched when the OK button is clicked and the value is saved.
		 * @type {{ value: string, error: { type: string, message: string } }}
		 */
		'NOW_RECORD_DATE_PICKER#VALUE_CHANGED': {}
	},
	transformState
});
